
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'

import {objectivelists, deleteById} from '@/api/objective.js'
import {resumedetail} from '@/api/ucenter.js'
import loadMore from '@/components/loadmore'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
components: {
loadMore,
LoadMore,
Confirm,
Alert
},
directives: {
TransferDom
},
created () {
this.getrecruiment()
resumedetail().then(res => {
if (res.data.result == 0) {
this.status = res.data.datas.status
}
})
},
data () {
return {
messageShow: false,
resMessage:　'',
showConfirm: false,
showColse: false,
historylists: [],
orderId: '',
total: 0,
loading: false,
allLoaded: false,
bottomText: '',
endtext: '',
nowId: '',
status: ''
}
},
methods: {
loadBottom: function() {
if (!this.endtext) {
this.loading = true;
}
this.orderId = this.historylists[this.historylists.length - 1].orderId
if (this.orderId == 0) {
this.loading = false;
this.allLoaded = true;
return;
}
this.getrecruiment()
},
handleBottomChange(val) {
this.bottomText = val;
},
getrecruiment () {
let fdata = {
"pagesize": 10,
"pageLastId": this.orderId

}
objectivelists(fdata).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.historylists.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.historylists.push(item)
})
}, 600)
}

}
})
},
confirm (i) {
this.nowId = i
this.showConfirm = true
},
close (i) {
this.nowId = i
this.showColse = true
},
closeobj () {

},

deleteobj () {

let fdata = {
id: this.nowId
}
deleteById(fdata).then(res => {
if (res.data.result == 0) {

this.orderId = ''
this.historylists = []
this.getrecruiment()
}
})
},

toedit (item) {
this.$router.push(
{
name: 'editIntent',
params: {'objitem': item}
}
)

},
newobjective () {
if (this.status == 2) {
this.$router.push('/editIntent')
} else {
this.messageShow = true
this.resMessage = '您的简历尚未发布'
}

}

}
}



